<template>
	<div class="_full_router fund-manage-con">
			<custHeader :title="title"></custHeader>
			<div class="fund-manage-contain form-submit">
				<div v-show="flowId==1">
					<mt-field label="新交易密码" placeholder="请输入新的交易密码" v-model="fundPassword" type="password" v-validtype="rules.password" :state="rules.password.state"></mt-field>
					<mt-field label="确认交易密码" placeholder="请再次输入交易密码" v-model="fundPasswordCom" type="password" v-validtype="rules.password2" :state="rules.password2.state"></mt-field>
					<mt-button  type="primary" size="large"  @click="setPassword" :disabled="valiSuccess">提交</mt-button>
				</div>
			<mt-popup
	          v-model="selectTypeVisible"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
	          <mt-picker :slots="selectTypeSlots"  valueKey="name" @change="selectTypeChange" :visible-item-count="5"></mt-picker>
	       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
	          	<button class="btn" @click="sureSelectType">确认</button>
	          	</div>
        	</mt-popup>

        	<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
        </div>
	</div>
</template>
<script>
import {Field,Picker,Popup,MessageBox,Actionsheet,Toast} from 'mint-ui'
import custHeader from  'components/custHeader'
import zjz from  'assets/2276.png'
import cookieUtil from 'js-cookie'

Vue.component(Actionsheet.name,Actionsheet);
Vue.component(Field.name,Field)
Vue.component(Popup.name,Popup)
Vue.component(Picker.name,Picker)
export default{
	data(){
		return {
			picMsg:"",

			dicts:{
                idType:{
                    name:'ID_TYPE',
                    dataKey:"selectTypeSlots"
                },
				securityQues1:{
					name:"SECURITY_QUES",
					dataKey:"qustion1"
				},
				securityQues2:{
					name:"SECURITY_QUES",
					dataKey:"qustion2"
				},
				securityQues3:{
					name:"SECURITY_QUES",
					dataKey:"qustion3"
				}
		},
			actions:[{name:"拍照",
						method:()=>{
							this.selectPic();
						}
						},
						{name:"从相册选择",
						method:()=>{
							this.selectPic();
						}
						}
			],
			sheetVisible:false,
			zjzshow:false,
			zjz:zjz,
		flowId:"1",
		title:"重置交易密码",
        capMessage: '发送短信',
		idType:{name:"",value:""},
		// idName:"",
		idCode:"",
		custName:'',
		certValidDate:"",
		idIssAgcy:"",

		nationality:{name:"",value:""},
		province:{name:"",value:""},
		city:{name:"",value:""},
		area:{name:"",value:""},
		postcode:"",
		address:"",
		linkNaem1:"",
		linkNaem2:"",
		relation1:"",
		relation2:"",
		mobileTel1:"",
		mobileTel2:"",

		bankCode:"",
		bankbranch:"",
		bankNet:"",
		bankAcct:"",
		fundPassword:"",
		fundPasswordCom:'',

		selectTypeVisible:false,
		selectTypeSlots:[{
        flex:1,
        values:[{value:'0',name:'人民币2'},{value:'1',name:'美元2'},{value:'2',name:'港币2'}],
        className:"slots"
    }],
    rules:{

      password:{
        str:"required",
        state:'',
        message:""
	},
	password2:{
        str:"required,equals[fundPassword]",
        state:'',
        message:""
	},
	
  },
		}
	},
	created:function(){
	this.qryCustBaseInfo();
	this.qrySafeQuestion();
	},
	 components:{
    custHeader
  },
  computed:{
  	valiSuccess:function(){
  		return ( this.fundPassword!="" && this.fundPasswordCom!="")? false:true;
  	},
  },
	methods:{
		setPassword:function(){
			if(this.rules.password2.state=="error"){
        		 Toast({
	            message: this.rules.password2.message,
	            position: "bottom",
	            duration:global.TOASTTIME
	          });
        		return ;
        	}

			if(global.isOpenMd5){
              this.fundPassword=global.md5(this.fundPassword);
            }
            var pars={
                password:this.fundPassword,
                useScope:global.USE_SCOPE.FUNDPASSWORD,// 0登录/交易1资金业务
                authType:global.AUTH_TYPE,
				bcustCode:cookieUtil.get('userCode')
            }
            axiosUtil.upCltPasswordBack(pars).then(data=>{
                var data=data.data;
                if(data.errorcode==0){
                    	MessageBox.alert("重设成功").then(action=>{
                    		this.$router.push("/personal");
                    	})
                }else{
                    	MessageBox("提示",data.errormsg)
                }
            })
        },
		checkSafeInfo:function(){
			var pars={custName:this.custName,
					idType:this.idType.value,
					idCode:this.idCode,
					smsAuthCode:this.captcha,
				answer1:this.answer1,
				answer2:this.answer2,
				answer3:this.answer3,
			};
			axiosUtil.checkSafeInfo(pars).then(data=>{
				var data=data.data;
				if(data.errorcode==0){
					    this.flowId=3;
				}else{
					MessageBox("提示",data.errormsg)
				}
			})
		},
		qrySafeQuestion:function(){
			var pars={};
			axiosUtil.qrySafeQuestion(pars).then(data=>{
				var data=data.data;
				if(data.errorcode==0){
					this.qustion1=data.data.qustion1;
					this.qustion2=data.data.qustion2;
					this.qustion3=data.data.qustion3;
				}else{
						MessageBox("提示",data.errormsg)
				}

				console.log(data,"--------------安全问题查询")
			})
		},
		 qryCustBaseInfo:function(){
        axiosUtil.qryCustBaseInfo().then(data=>{
          var data=data.data;
        //   console.log(data);
        this.idType.value=data.data.idType;
        if(data.errorcode==0){
			this.mb=data.data.mb;
        	// window.dict("ACCT_TYPE",data.data.idType).then(data=>{
        	// 	this.idType.name=data
			//
        	// });
        }else{
        	MessageBox("提示",data.errormsg)
        }

        })
      },
		upload:function(e){
			let files=e.target.files || e.dataTransfer.files;
			if(!files.length ) return;
			var pars=new FormData();
			pars.append("file",files[0],files[0].name);
			pars.append("uploadUser","279947");
			pars.append("docBizId","4001");
			pars.append("docBizSubid","1");
			axiosUtil.realNamePicUpload(pars).then(data=>{
			})


		},
		showSheet:function(){
			this.sheetVisible=true;
		},
		selectPic:function(){
			this.zjzshow=true;

		},
		selectTypeChange:function(picker,value){
			console.log(value);
			if(value[0]){
				this.idType.name=value[0].name;
                this.idType.value=value[0].value;
			}
		},

		dissSelectType:function(){
			this.currency='';
			this.selectTypeVisible=false;
		},
		sureSelectType:function(){
			if(!this.currency){
		        this.currency = this.selectTypeSlots[0].values[0].name;
		      }
		      this.selectTypeVisible = false
		},
		showFlow:function(index){
				this.flowId=index;
		},
        selectTypeVisibleShow:function(){
            this.selectTypeVisible=true;
        },
        send:function(){
          var that = this;
          // if(!(this.rules.mb.state !== 'error' &&  this.rules.mb.state !== '' && this.mb)){
          //   MessageBox("提示", "你的手机号格式不正确");
          //   return false
          // }
					this.mb = cookieUtil.get("mb")
          axiosUtil.sendSms({
            askBizCode: 100220,//注册验证吗 和找回密码验证码
            mb:this.mb
          }).then(function(data){
            if (data.errorcode == 1) {
              MessageBox("提示", data.errormsg);
            } else{
              let time = 60;
              that.capMessage = time + "s后重新发送"
              that.idDisabled = true;
              let timer = setInterval(function(){
                time = time - 1;
                that.capMessage = time + "s后重新发送";
                if(!time){
                  // time = 60
                  that.capMessage = '发送短信'
                  that.idDisabled = false;
                  clearInterval(timer)
                }
              },1000)
            }
          })

        },
		// realNameAsk:function(){
		// 		 if(global.isOpenMd5){
	    //     this.fundPassword=global.md5(this.fundPassword);
	    //   }
		// 	var pars={
		// idType:this.idType.value,
		// idName:this.idName,
		// idCode:this.idCode,
		// certValidDate:this.certValidDate,
		// idIssAgcy:this.idIssAgcy,
		//
		// nationality:this.nationality.value,
		// province:this.province.value,
		// city:this.city.value,
		// county:this.area.value,
		// postcode:this.postcode,
		// address:this.address,
		// linkNaem1:this.linkNaem1,
		// linkNaem2:this.linkNaem2,
		// relation1:this.relation1,
		// relation2:this.relation2,
		// mobileTel:this.mobileTel,
		// mobileTel2:this.mobileTel2,
		//
		// bankCode:this.bankCode,
		// bankbranch:this.bankbranch,
		// bankNet:this.bankNet,
		// bankAcct:this.bankAcct,
		// fundPassword:this.fundPassword,
		//
		//
		// 	}
		//
		// 	axiosUtil.realNameAsk(pars).then(data=>{
		// 		var data=data.data;
		// 		if(data.errorcode==1){
		// 			 MessageBox("提示", data.errormsg);
		// 		}else{
		// 			MessageBox("提示", data.errormsg);
		// 		}
		//
		// 	})
		// }
	}
}

</script>
<style scoped>
.fund-manage-con{

}
.fund-manage-contain{
	padding: 12px;
	overflow-y:auto;
	    height: 100%;
    padding-bottom: 48px;
}
.select-dict{
	font-size: 14px;
}
	.select-dict button{
		    width: 50%;
    float: left;
    height: 28px;
    border: none;
    background: #fff;
    border-top: 1px solid #ccc;
	}
	.select-dict button:last-child{
		border-left: 1px solid #ccc;
	}
	.select-dict-btns{
		position: absolute;
		bottom: 0;
		bottom: -28px;
    width: 100%;
	}
	.zjz{
		width: 100%;
	}
</style>



